
import { defineComponent, ref } from "vue";
import { search, QA, Paginator } from "@/api/qa";
import PaginationComp from "@/components/PaginationComp.vue";
import qaComp from "@/components/qaComp.vue";

export default defineComponent({
  name: "Search",
  components: { PaginationComp, qaComp },
  setup() {
    let q = ref("");
    let qas = ref<Array<QA>>([]);
    let paginator = ref<Paginator>({ current: 1, all: 1 });
    let loading = ref(false);
    let error = ref(false);
    let errorText = ref("");
    const searchMethod = (page: number) => {
      loading.value = true;
      qas.value = [];
      search(q.value, page)
        .then((data) => {
          qas.value = data;
          // paginator.value = data[1];
        })
        .catch((err) => {
          error.value = true;
          errorText.value = err;
        })
        .finally(() => {
          loading.value = false;
          window.scrollTo(0, 0);
        });
    };

    return {
      q,
      qas,
      loading,
      error,
      errorText,
      searchMethod,
      paginator,
    };
  },
});
